import React, { Component } from "react";

class Journal extends Component {
  render() {
    return (
      <div className="Blog">
        <div className="hero">
          <h1>
            Blog
          </h1>
          <h3>
            Welcome to my Blog
          </h3>
          <p>
            This is where I write my thoughts, creations and experiences.
          </p>
        </div>
      </div>
    );
  }
}

export default Journal;
